import { useState } from 'react';

const Sidebar = ({ setMenuItem }) => {
  const [isOpen, setIsOpen] = useState(false);
  
  const menuItems = [
    'Treino_Abdomen',
    'Treino_Bicpes',
    'Treino_Costas',
    'Treino_Ombro',
    'Treino_Peito',
    'Treino_Pernas',
    'Treino_Triceps',
  ];

  return (
    <div className="lg:flex lg:flex-col bg-gray-800 text-white p-4 w-64 h-screen">
      <button
        onClick={() => setIsOpen(!isOpen)}
        className="lg:hidden text-white mb-4"
      >
        {isOpen ? 'Fechar Menu' : 'Abrir Menu'}
      </button>
      {(isOpen || window.innerWidth >= 1024) && (
        <ul>
          {menuItems.map((item) => (
            <li
              key={item}
              className="cursor-pointer py-2 px-4 hover:bg-gray-700"
              onClick={() => setMenuItem(item)}
            >
              {item}
            </li>
          ))}
        </ul>
      )}
    </div>
  );
};

export default Sidebar;
